.card__collection .cards--one::after {
  content: '';
  display: block;
  position: absolute;
  height: 70px;
  -webkit-transform: rotate(-3deg);
          transform: rotate(-3deg);
  background: #e660e3;
  position: absolute;
  bottom: -80px;
  left: 0;
  right: -10px;
  z-index: 9;
  -webkit-transition: all .2s ease-in;
  transition: all .2s ease-in;
